const { useRef, useEffect } = require('react');

/**
 * @param {string} title ÊñáÊ°£Ê†áÈ¢ò
 * @param {boolean} [retainOnUnmount] È°µÈù¢Âç∏ËΩΩÂêé‰øùÁïôÊ†áÈ¢òÔºü
 */
export function useDocumentTitle(title, retainOnUnmount = false) {
  const defaultTitle = useRef(document.title);

  useEffect(() => {
    document.title = title;
  }, [title]);

  useEffect(() => {
    const title = defaultTitle.current;
    return () => {
      if (!retainOnUnmount) {
        document.title = title;
      }
    };
  }, [retainOnUnmount]);
}
